.Burger{
   height: 40px;
   margin: 7px 0;
   float: right;
   width: 45px;
   cursor: pointer;
}

.Burger:focus{
   outline: none;
}

.bar{
   width: 80%;
   height: 5px;
   margin: 4.2px;
   background-color: rgb(151, 151, 151);
   border: 1px solid rgb(87, 87, 87);
   border-radius: 5px;
}

@media screen and (min-width:700px){
   .Burger{
      display: none;
   }
}